import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, ConfigMulta } from '../../../../models/dtos/mlf/configMulta';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'dtInclusao',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const MultaSearchView: FC<SearchViewProps<ConfigMulta>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'dtInclusao',
      editable: false,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Data de Inclusão',
    },
    {
      dataField: 'taxa',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Taxa Diária',
    },
    {
      dataField: 'limite',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Limite',
    },
  ];
  return (
    <WindowComponent title="Manutenção de Multa" wikiId={1009}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'MultaEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default MultaSearchView;
